import React from 'react'

import CommentsSvg from './../../icons/Comments.svg'
import Comment from './Comment/Comment';
import './comments.css';

function Comments() {
  return (
    <section className='comments_bg'>
      <div className="comments">
        <img className="comments_img" src={CommentsSvg} alt="Comments icon" />
        <div className="comments_title">  </div>
        <div className="comments_box">
          <div className='comments_wrapper'>
            <Comment
              name={''}
              desc={'      .                .         .'}
              sub={', '} />
            <Comment sub={', '} name={''} desc={'    -  !   ,         .     .'} />
            <Comment sub={', '} desc={`     .      .                . CentrMed !`} name={' '} />
          </div>
        </div>
      </div>
    </section>
  )
}

export default Comments